import './App.scss';

import { HashRouter as HRouter, BrowserRouter as Router } from 'react-router-dom';

import Header from './components/1_header/Header';
import Body from './components/2_body/Body';
import Footer from './components/3_footer/Footer';

function App() {
  return (
    <HRouter>
      <div id='app-router'>
        <Header />
        <Body />
        <Footer />
      </div>
    </HRouter>
  );
}

export default App;
