import React from 'react'
import Carousel from 'react-bootstrap/Carousel'
import 'bootstrap/dist/css/bootstrap.min.css';

const TopSlider = () => {
    return (
        <div id='topslider'>
            <Carousel 
                    fade={false}
                    touch={true}>
                {
                    ["#4c686b", "#701247", "#32a852"].map(el =>
                        <Carousel.Item interval={1000000} key={el} style={{ backgroundColor: el }}>
                            <Carousel.Caption>
                                <div>
                                    <h3>Slide Image {el}</h3>
                                </div>
                            </Carousel.Caption>
                        </Carousel.Item>
                    )
                }
            </Carousel>
        </div>
    )
}

export default TopSlider
